.sidebar {
    // outline: 1px solid #333
    width: 199px;
    padding: 56px 0 0 20px;
    border-right: 1px solid #F1F1F1;
    &__list {
        display: flex;
        flex-direction: column; }
    &__item {
        display: flex;
        align-items: center;
        padding: 10px 14px 11px 12px;
        font-family: Roboto;
        font-style: normal;
        font-weight: bold;
        &-color {
            width: 10px;
            height: 10px;
            background-color: #4DD599;
            margin-right: 10px;
            border-radius: 50%; }
        &-img {
            margin-left: auto; } }
    &__item:hover {
        background: #FFFFFF;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.05);
        border-radius: 4px; }
    &__btn {
        margin-top: 27px; }
    &-form {
        position: absolute;
        right: -8px;
        width: 235px;
        padding: 20px;
        background-color: #FFFFFF;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.05);
        border-radius: 10px;
        display: flex;
        &__wrapper {
            margin-top: 3px;
            position: relative;
            width: 235px; }
        &__text {
            width: 200px;
            height: 32px;
            margin-bottom: 13px;
            padding-left: 11px; }
        &__checkbox {
            margin-left: 3px; }
        &__btn {
            width: 200px;
            height: 37px;
            margin-top: 13px; }
        &__close {
            position: absolute;
            border-radius: 50%;
            background: #5C5C5C;
            color: #fff;
            width: 20px;
            height: 20px;
            border: none;
            top: -5px;
            right: -5px; } } }

@media screen and (max-width: 900px) {
    .sidebar {
        width: 35vw;
        padding-left: 0;
        &__item {
            font-size: 12px; }
        &__btn {
            font-size: 0.7em;
            width: 25vw; } } }

@media screen and (max-width: 670px) {
    .sidebar {
        width: 35vw;
        padding-left: 0;
        &__item {
            &-color {
                display: none; } } } }
